import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';

import '../styles/header.scss';

const IMG_URL = 'https://d3i4yxtzktqr9n.cloudfront.net/web-eats-v2';

const Header = () => {
  const [initialLocation, setInitialLocation]
    = useState(localStorage.getItem('location') || 'London');

  const [location, setLocation] = useState(initialLocation);
  const [editing, setEditing] = useState({ location: false, search: false });

  const inputRef = useRef();

  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    if (editing.location) {
      inputRef.current.location.focus();
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  });

  const handleClick = () => {
    setEditing({ ...editing, location: true });
  };

  const handleChange = (event) => {
    setLocation(event.target.value);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const { value } = event.target.location;

    if (value) {
      localStorage.setItem('location', value);

      setInitialLocation(localStorage.getItem('location'));
      setEditing({ ...editing, location: false });
    }
  };

  const handleClickOutside = (event) => {
    if (inputRef.current && !inputRef.current.contains(event.target)) {
      setLocation(initialLocation);
      setEditing(false);
    }
  };

  return (
    <header className="header">
      <div className="container header__container">
        <Link to="/">
          <img
            src={`${IMG_URL}/f8f0721f871b3704cce92eb96bc6e504.svg`}
            alt="Uber Eats"
          />
        </Link>

        {
          editing.location
          && (
            <form
              action=""
              onSubmit={event => handleSubmit(event)}
              ref={inputRef}
              className="header__location header__location-form"
            >
              <input
                type="text"
                name="location"
                value={location}
                onChange={event => handleChange(event)}
              />
            </form>
          )
        }

        {
          editing.location
          || (
            <button
              type="button"
              onClick={() => handleClick()}
              className="header__location"
            >
              {location}
            </button>
          )
        }

        <button type="button" className="header__delivery">Deliver now</button>

        {
          editing.search
          && (
            <form
              action=""
              onSubmit={event => handleSubmit(event)}
              ref={inputRef}
            >
              <input
                type="text"
                name="search"
                value={location}
                onChange={event => handleChange(event)}
              />
            </form>
          )
        }
        {
          editing.search
          || <button type="button" className="header__search">Search</button>
        }

        <a href="#" className="header__sign-in">Sign in</a>

      </div>
    </header>
  );
};

export default Header;
