import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

import '../styles/main.scss';

const API_URL = 'https://mate-academy.github.io/react_uber-eats/api/location/ChIJdd4hrwug2EcRmSrV3Vo6llI.json';
const IMG_URL = 'https://d3i4yxtzktqr9n.cloudfront.net/web-eats-v2';

const Main = () => {
  const [restaurants, setRestaurants] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    axios.get(API_URL)
      .then((res) => {
        const result = res.data.data;
        const rests = result.feedItems.map(item => result.storesMap[item.uuid]);

        setRestaurants(rests);
        setLoading(false);
      });

    return () => setLoading(true);
  }, []);

  return (
    <main>
      <div className="container">
        <div className="grid-container">
          {
            loading
              ? <div>Loading</div>
              : (
                <>
                  {
                    restaurants.map(rest => (
                      <div key={rest.uuid} className="card">
                        <img
                          src={rest.heroImageUrl}
                          alt={rest.title}
                          className="card-image"
                        />
                        <h3 className="card-title">{rest.title}</h3>
                        <span
                          className="card-breadcrumbs"
                        >
                          {rest.categories.join('•')}
                        </span>

                        <div>
                          {
                            rest.etaRange
                              ? <div className="card-time">{rest.etaRange.text}</div>
                              : <div className="card-time">50-60min</div>
                          }
                          <div className="card-rating">
                            {
                              rest.feedback
                                ? (
                                  <>
                                    {rest.feedback.rating}
                                    <img
                                      src={`${IMG_URL}/92367108b11b8ee48b6f29cb3fef2d4d.svg`}
                                      alt="star"
                                    />
                                    {rest.feedback.ratingCount}
                                  </>
                                )
                                : 'No rating'
                            }
                          </div>
                        </div>
                      </div>
                    ))
                  }
                </>
              )
          }
        </div>
      </div>
    </main>
  );
};

export default Main;
